.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  position: fixed;
  width: 100%;
  top: 0;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.App-footer{
  position: fixed;
  width: 100%;
  bottom: 0;
}

.App-layout-list{
  width: 100%;
  overflow-y: hidden;
}

.App-layout{
  display: -ms-flexbox;
  display: flex;
  background-color: #eeeeee;
  -ms-flex-direction: column;
  flex-direction: column;
}


.App-layout-content{
  -ms-flex: 1;
      flex: 1 1;
  /* display: flex; */
  /* flex-wrap: wrap; */
  /* justify-content: space-around; */
  overflow-y: auto; 
  -webkit-overflow-scrolling: touch;/**兼容ios设备,允许独立的滚动区域和触摸回弹*/ 
  
}

.App-cover-inner{
  width: 4rem;
  height: 5rem;
  border: 1px solid #ebebeb;
  background-position: 50%;
  background-size: 100%;
  overflow: hidden;
}

body{
  overflow: hidden;
}

a {
  text-decoration: none;
}
a:link {
  text-decoration: none;
}
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
a:active {
  text-decoration: none;
}



